html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  vertical-align: baseline;
  font-size: 100%;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

input {
  outline: none;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
  font: inherit;
  border: 0;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

:before, :after {
  box-sizing: border-box;
}

svg {
  color: inherit;
}

button {
  -webkit-appearance: none;
  text-align: inherit;
  box-shadow: none;
  cursor: pointer;
  color: inherit;
  font: inherit;
  background: none;
  border: none;
  border-radius: 0;
  padding: 0;
}

.theme-dark {
  --color-primary: #fff;
  --color-secondary: #e0e0e0;
  --color-accent: #149cff;
  --color-bkg: #121212;
  --color-subaccent: #144b59;
  background-color: var(--color-bkg);
}

.theme-light {
  --color-primary: #333;
  --color-secondary: #4f4f4f;
  --color-accent: #149cff;
  --color-bkg: #fff;
  --color-subaccent: #144b59;
  background-color: var(--color-bkg);
}

body {
  min-width: 250px;
  min-height: 100%;
}

a {
  all: unset;
  cursor: pointer;
}

.l-navbar, .l-header, .l-content, .l-newsletter, .l-footer {
  max-width: 90rem;
  margin-inline: auto;
  padding-block: 1.5rem;
  padding-inline: max(1.5rem, 6%);
}

.l-navbar {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.l-header {
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 4rem 3rem;
  margin-bottom: 2rem;
  display: flex;
}

.l-header__hero, .l-header__top3 {
  flex: 25rem;
}

.l-header__top3 {
  margin-top: 5%;
}

.l-content {
  margin-bottom: 2rem;
}

.l-news-grid {
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  gap: 1.5rem;
  max-width: 100%;
  margin-bottom: 1.5rem;
  display: grid;
}

.l-newsletter {
  margin-bottom: 2rem;
}

.l-newsletter__wrapper {
  flex-wrap: wrap-reverse;
  gap: 2rem;
  padding-block: 1.5rem;
  display: flex;
}

@font-face {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.a7d3aa76.ttf") format("truetype");
}

@font-face {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.c65ded33.ttf") format("truetype");
}

@font-face {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/OpenSans-SemiBold.eaf8bd4e.ttf") format("truetype");
}

@font-face {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.8317c8c5.ttf") format("truetype");
}

.f-heading {
  font-family: Montserrat, sans-serif;
  font-style: normal;
  line-height: 1.2;
}

.f-heading--S, .f-heading--M {
  letter-spacing: -.01rem;
  font-weight: 600;
}

.f-heading--S {
  font-size: 1.5rem;
  font-weight: 600;
}

.f-heading--M {
  font-size: 2rem;
}

.f-heading--L {
  letter-spacing: -.03rem;
  font-size: 3rem;
  font-weight: 700;
}

.f-body {
  font-family: Open Sans, sans-serif;
  font-style: normal;
  line-height: 1.5;
}

.f-body--S {
  font-size: .8rem;
  font-weight: 400;
}

.f-body--M {
  font-size: 1rem;
  font-weight: 400;
}

.f-body--L {
  font-size: 1rem;
  font-weight: 600;
}

.f--center {
  text-align: center;
}

.navlogo {
  color: var(--color-primary);
  align-items: center;
  display: flex;
}

.navlogo__logo {
  width: 2.625rem;
  height: 2.625rem;
}

.navlogo__name > span {
  display: none;
}

.navmenu {
  color: var(--color-primary);
  position: relative;
}

.navmenu__btn {
  cursor: pointer;
  font-size: 2rem;
  line-height: 0;
  display: block;
}

.navmenu__items {
  z-index: 10;
  background-color: var(--color-bkg);
  border: 1px solid;
  flex-direction: column;
  gap: .5rem 1rem;
  padding: 1rem;
  display: none;
  position: absolute;
  bottom: -1rem;
  right: 0;
  overflow: hidden;
  transform: translateY(100%);
}

.theme-input {
  width: min-content;
  font-size: 1rem;
  display: block;
  position: relative;
}

.theme-input__check {
  visibility: hidden;
  position: absolute;
}

.theme-input__wrapper {
  border: 1px solid var(--color-primary);
  border-radius: 1rem;
  align-items: center;
  gap: 1rem;
  width: 100%;
  padding: .25em;
  display: flex;
  position: relative;
  overflow: hidden;
}

.theme-input__wrapper:after {
  content: "";
  border: 1px solid var(--color-primary);
  border-radius: 50%;
  width: 1.5em;
  height: 1.5em;
  transition: left .2s;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-input__moon {
  visibility: visible;
}

.theme-input__sun {
  visibility: hidden;
}

.theme-input__check:checked ~ .theme-input__wrapper:after {
  transition: left .2s;
  left: calc(100% - 1.5em);
}

.theme-input__check:checked ~ .theme-input__wrapper .theme-input__sun {
  visibility: visible;
}

.theme-input__check:checked ~ .theme-input__wrapper .theme-input__moon {
  visibility: hidden;
}

.hamburger__line {
  transform-origin: center;
  transition: y .3s ease-in .3s, transform .3s ease-in, opacity 0s .3s;
}

.hero {
  z-index: 0;
  position: relative;
}

.hero__title-top {
  mix-blend-mode: difference;
  z-index: 2;
  color: var(--color-primary);
  font-size: clamp(2.5rem, 7vw, 6rem);
  line-height: 1;
  position: relative;
  top: .4em;
  left: 0;
}

.hero__img {
  z-index: 1;
  width: 100%;
  max-width: 600px;
  position: relative;
}

.hero__title-center {
  z-index: 2;
  background: #fff1f180;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1.25rem .75rem;
  font-size: clamp(1rem, 8vw, 3rem);
  position: absolute;
  top: 50%;
  right: -1rem;
}

.hero__title-bottom {
  z-index: 2;
  text-align: right;
  color: var(--color-primary);
  mix-blend-mode: difference;
  font-size: clamp(2.5rem, 23vw, 6.1875rem);
  line-height: .8;
  position: relative;
  bottom: .2em;
  right: -1rem;
}

.top3 {
  color: #fff;
  overflow: hidden;
}

.top3__title {
  color: var(--color-accent);
  margin-bottom: 1.25rem;
}

.section-title {
  color: var(--color-primary);
  border-top: 1px solid var(--color-primary);
  border-bottom: 1px solid var(--color-primary);
  text-align: center;
  width: 100%;
  margin-bottom: 1.5rem;
  padding-block: 1rem;
}

.card {
  grid-template: "number title" min-content
                 "number author" min-content
                 "number url" min-content
                 "number time" min-content
                 / min-content auto;
  gap: .5rem 1rem;
  max-width: 100%;
  padding: 2rem;
  display: grid;
  position: relative;
  overflow: hidden;
}

.card__number {
  color: var(--color-secondary);
  grid-area: number;
  width: min-content;
}

.card__title {
  color: var(--color-primary);
  text-overflow: ellipsis;
  grid-area: title;
  display: block;
  overflow: hidden;
}

.card__author {
  grid-area: author;
}

.card__time {
  grid-area: time;
  text-decoration: underline;
}

.card__url {
  text-overflow: ellipsis;
  white-space: nowrap;
  grid-area: url;
  overflow: hidden;
}

.card__author, .card__time, .card__url {
  color: var(--color-secondary);
  align-self: start;
}

.card.loading {
  height: 10rem;
}

.card.loading:after {
  content: "";
  border: 5px solid #ffffff24;
  border-top: 6px solid var(--color-accent);
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  animation: 1s linear infinite spin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.card--line-top {
  border-top: 1px solid var(--color-secondary);
}

.card--line-bottom {
  border-bottom: 1px solid var(--color-secondary);
}

.card--accent {
  text-overflow: inherit;
  color: var(--color-accent);
  white-space: inherit;
  overflow: hidden;
}

.news-navigation {
  color: var(--color-primary);
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin-inline: auto;
  display: flex;
}

.news-navigation__list {
  align-items: center;
  gap: .5rem;
  display: flex;
}

.news-navigation__item {
  cursor: pointer;
  display: block;
}

.news-navigation__item.selected {
  color: var(--color-accent);
  scale: 1.2;
}

.news-navigation__item:hover, .news-navigation__btn:hover {
  scale: 1.2;
}

.newsletter-form, .newsletter-text {
  flex: 18.75rem;
}

.newsletter-form__label {
  color: var(--color-primary);
  padding-inline: .5rem;
  display: block;
}

.newsletter-form__input {
  color: #000;
  border: 1px solid var(--color-primary);
  background-color: #fff;
  border-radius: 0;
  width: 100%;
  margin-bottom: 1.5rem;
  padding: .7rem 1rem;
  display: block;
}

.newsletter-form__input::placeholder {
  color: #3336;
}

.newsletter-form__btn {
  color: #fff;
  background-color: var(--color-accent);
  margin-left: auto;
  padding: .7rem 1rem;
  display: block;
}

.newsletter-text {
  padding-inline: .5rem;
}

.newsletter-text__head {
  color: var(--color-primary);
  margin-bottom: .62rem;
}

.newsletter-text__body {
  color: var(--color-secondary);
}

.footer {
  border-top: 1px solid var(--color-primary);
  padding-block: 4rem;
}

.footer__title {
  color: var(--color-primary);
  justify-content: center;
  align-items: center;
  gap: .5rem;
  margin-bottom: .62rem;
  display: flex;
}

.footer__title span {
  height: 1em;
}

.footer__info {
  color: var(--color-secondary);
  margin-bottom: .62rem;
}

.navmenu__btn.is-active .hamburger__line {
  transition: y .3s ease-in, transform .3s ease-in .3s, opacity 0s .3s;
}

.navmenu__btn.is-active .hamburger__top, .navmenu__btn.is-active .hamburger__bottom {
  y: 10.75px;
}

.navmenu__btn.is-active .hamburger__top {
  transform: rotate(45deg);
}

.navmenu__btn.is-active .hamburger__middle {
  opacity: 0;
}

.navmenu__btn.is-active .hamburger__bottom {
  transform: rotate(-45deg);
}

.navmenu__btn.is-active ~ .navmenu__items {
  visibility: visible;
  display: flex;
}

@media (width >= 26.25rem) {
  .l-news-grid {
    grid-template-columns: repeat(auto-fit, minmax(18.75rem, 1fr));
  }
}

@media (width >= 62.5rem) {
  .l-news-grid {
    grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  }

  .navmenu__btn {
    display: none;
  }

  .navmenu__items {
    border: none;
    border-bottom: 2px solid var(--color-secondary);
    flex-direction: row;
    padding: .75rem 2rem;
    display: flex;
    position: static;
    transform: none;
  }

  .navmenu__items > :not(:last-child):after {
    content: "/";
    color: var(--color-accent);
    font: inherit;
    margin-left: 1rem;
  }
}

@media (width >= 75rem) {
  .l-navbar, .l-header, .l-content, .l-newsletter, .l-footer {
    padding-block: 2rem;
    padding-inline: min(7.5rem, 8%);
  }

  .navlogo__name > span {
    margin-left: .5rem;
    display: inline;
  }
}

@media (hover: hover) {
  .card__head:hover {
    color: var(--color-accent);
    text-decoration: underline;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

